.advantages {
  background: rgb(242, 242, 242);
  padding: 150px 0 170px;
  @include r(767) {
    padding: 50px 0; }
  &__block {
    border-radius: 10px;
    box-shadow: 0px 1.5px 20px 0 rgba(0, 0, 0, 0.06);
    background-color: $white;
    display: flex;
    align-items: center;
    padding: 60px 50px;
    height: 100%;
    margin: 0 12px;
    @include r(1040) {
      margin: 0;
      margin-bottom: 40px;
      height: auto; }
    @include r(550) {
      flex-direction: column;
      padding: 30px 20px; }
    &-primary {
      padding: 60px 60px;
      position: relative;
      &:before {
        content: 'VS';
        text-transform: uppercase;
        @include center;
        width: 25px;
        height: 25px;
        background-color: $pink-red;
        font-family: 'Open Sans';
        font-size: 12px;
        font-weight: 800;
        color: $white;
        border-radius: 100%;
        position: absolute;
        top: 50%;
        right: -33px;
        transform: translate(0, -50%);
        @include r(1040) {
          right: auto;
          left: 50%;
          top: auto;
          bottom: -33px;
          transform: translate(-50%, 0); } } }
    &-secondary {
      li {
        background-color: rgba(#fde7ef, 0.6) !important; } }
    img {
      @include r(1200) {
        width: 45%; }
      @include r(550) {
        margin-bottom: 20px; } }
    &__text {
      padding-left: 40px;
      @include r(550) {
        padding-left: 0; }
      h3 {
        font-size: 18px;
        font-weight: 800;
        text-align: left;
        color: $dark-two;
        text-transform: uppercase;
        margin-bottom: 24px;
        b {
          color: #e52360;
          text-transform: uppercase; } }
      li {
        border-radius: 7.5px;
        background-color: $white-three;
        padding: 15px;
        font-size: 12px;
        font-weight: 600;
        line-height: 1.67;
        color: $dark;
        margin-bottom: 10px; } } } }
