.intro {
  height: 795px;
  background: $dark-two;
  @include center;
  @include r(767) {
    height: 650px; }
  strong {
    font-size: 16px;
    font-weight: bold;
    line-height: 1.56;
    color: $cerise;
    text-transform: uppercase;
    margin-bottom: 25px;
    @include r(767) {
      font-size: 12px; } }
  h2 {
    font-size: 30px;
    font-weight: 300;
    line-height: 1.67;
    letter-spacing: 1.2px;
    color: $white;
    margin-bottom: 75px;
    @include r(767) {
      font-size: 20px; } }
  &__btn {
    display: flex;
    justify-content: center;
    @include r(450) {
      flex-direction: column; }
    .btn-primary {
      margin-right: 10px;
      @include r(450) {
        margin: 0;
        margin-bottom: 20px; } } } }
